.header-color {
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.landing-grid{
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  text-align: center;
  position: fixed;
  top: 2;
  left: 0;
  width: 100%;
  height: 100%;
}

.jake-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .7;
  width: 80%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 80%;
  margin: auto; 
}
.banner-text p{
  font-size: 20px;
  padding: 1em;
  color: white;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width:50%;
  margin: auto;
}

.social-links i {
  color:white;
  font-size:5em;
  padding: 8px;

}

.project-grid {
  display: flex;
}

.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}
.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: "oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 100%;
  margin: auto;
}

.contact-list i {
  font-size:66px;
  padding-right: 5rem;
}
/* Resume Page css*/
.resume-right-col {
  border-block-end-color: #155799;
  padding: 2em;
}

.about-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
}

/*About grid*/
.about-grid{
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
  position: fixed;  
}
.about-banner-text{
  background-color: black;
  margin: auto;
  opacity: .7;
  width: 80%;
  border-radius: 10px;
}

.jake-img2 {
  width: 500px;
  height: 550px;
  padding-left: 5em;
}

